.top {
  height: calc(100vh - 100px);
  width: 100%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  text-align: center;
  min-height: 515px;
}

.top * {
  color: white;
}

.top h1 {
  font-weight: 400;
  margin-left: 20px;
  margin-right: 20px;
}

.top__button {
  background: transparent linear-gradient(78deg, #22242F 0%, #262834DE 54%, #464B5D 100%) 0% 0% no-repeat padding-box;
  padding: 20px 35px;
  font-weight: 300;
  font-size: 1.125rem;
  display: flex;
  align-items: center;
  margin: 5vw auto 3vw;
  cursor: pointer;
  border: 1px solid transparent;
  transition: all .4s;
}

.top__button:hover {
  border: 1px solid white;
}

@media (max-width: 93.74em) {
  .top__name svg {
    width: 80vw;
  }
}

@media (max-width: 61.99em) {
  .top {
    height: calc(100vh - 70px);
  }
  .top__button {
    margin: 5vw auto 5vw;
  }
}

@media (max-width: 35.99em) {
  .top {
    height: calc(100vh - 60px);
  }
}

.arrow-down {
  border-radius: 50%;
  border: 1px solid white;
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  margin-left: 20px;
}

.arrow-down::before {
  width: 15px;
  height: 15px;
  content: '';
  display: block;
  border-left: 1px solid white;
  border-bottom: 1px solid white;
  transform: rotate(-45deg);
  margin-top: 7px;
}

.two-columns {
  margin-top: 100px;
  display: flex;
}

.two-columns > div {
  display: flex;
  flex-direction: column;
  width: 50%;
}

.two-columns__left {
  justify-content: space-between;
  margin-top: 100px;
}

.two-columns__left > div {
  padding: 50px 10%;
  width: 85%;
  border-right: 2px solid #b2b2b2;
}

.two-columns__left img {
  width: 95%;
}

.two-columns__right {
  place-items: end;
  justify-content: space-between;
}

.two-columns__right > div {
  width: 85%;
  border-left: 2px solid #b2b2b2;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 5vw;
}

.two-columns__right img {
  width: 95%;
}

.two-columns__right a {
  margin-top: 30px;
  font-family: "Proza Libre", sans-serif;
}

.text {
  margin-top: 150px;
  display: flex;
  flex-direction: column;
  max-width: 930px;
  text-align: center;
  align-self: center;
}

.three-columns {
  display: flex;
  justify-content: space-between;
  margin-top: 145px;
}

.three-columns img {
  width: 29vw;
  height: 32vw;
}

.three-columns__left {
  margin-top: 45px;
}

.three-columns__right {
  margin-top: -75px;
}

.three-columns__center {
  width: 40%;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.three-columns__center p {
  width: 80%;
  font-size: 0.9375rem;
}

.three-columns h3 {
  padding-bottom: 10px;
  text-align: end;
  position: relative;
  margin-bottom: 40px;
}

.three-columns h3:after {
  content: '';
  position: absolute;
  border-bottom: 2px solid #b2b2b2;
  width: 200%;
  bottom: 0;
  z-index: -1;
}

.three-columns h3.decorator-left:after {
  left: 0;
}

.three-columns h3.decorator-right:after {
  right: 0;
}
